// Sass imports
import 'element-ui/lib/theme-chalk/index.css';
import '../sass/main.sass';
import 'ionicons/dist/css/ionicons.min.css';
// require styles
import 'codemirror/lib/codemirror.css';
import { EventBus, ElectronBus } from '@vue-components/event-buses/EventBuses';
import '@vue-components/pages/main/main.vue';
import Vue from 'vue';
Vue.directive('click-outside', {
    bind: function (el, binding, vnode) {
        el.clickOutsideEvent = function (event) {
            // here I check that click was outside the el and his childrens
            if (!(el == event.target || el.contains(event.target))) {
                // and if it did, call method provided in attribute value
                vnode.context[binding.expression](event);
            }
        };
        window.addEventListener('click', el.clickOutsideEvent);
        window.addEventListener('contextmenu', el.clickOutsideEvent);
    },
    unbind: function (el) {
        window.removeEventListener('click', el.clickOutsideEvent);
        window.removeEventListener('contextmenu', el.clickOutsideEvent);
    },
});
window.app = {
    EventBus: EventBus,
    ElectronBus: ElectronBus,
};
(function () {
    new Vue({
        el: '#app'
    });
})();
